import React, { useEffect, useState } from 'react';
import { Button } from 'reactstrap';
import { Checkmark } from '../Checkmark/Checkmark';

export const Repe = ({ id, pNumRepes, pWeigth, editable }) => {
  const [weigth, setWeigth] = useState(pWeigth);
  const [num, setNum] = useState(pNumRepes);
  const [isEditable, setIsEditable] = useState(editable);

  useEffect(() => {
    setIsEditable(editable);
    setWeigth(pWeigth);
    setNum(pNum);
  }, [editable, pWeigth, pNumRepes]);

  const handleOnclickEdit = e => {
    setIsEditable(!isEditable);
  };

  return (
    <tr>
      <th scope="row">{id}</th>
      <td>
        {isEditable ? <input value={num} onChange={event => setNum(event.target.value)} /> : num}
      </td>
      <td>
        {isEditable ? (
          <input value={weigth} onChange={event => setWeigth(event.target.value)} />
        ) : (
          weigth
        )}
      </td>
      <td>
        {!isEditable ? (
          <Button color="primary" onClick={handleOnclickEdit}>
            edit
          </Button>
        ) : (
          ''
        )}
      </td>
      <td>
        {isEditable ? (
          <Button color="danger" onClick={handleOnclickEdit}>
            done
          </Button>
        ) : (
          ''
        )}
      </td>
    </tr>
  );
};
